(function() {
    'use strict';

    ngApp.controller('containerConsoleCtrl', function($scope, $filter, $element, CONF, dockerApiSvr, containerSvr) {
        'ngInject';

        var containerId = $scope.containerId;

        $scope.isMax = false;
        $scope.tabs = [];

        var num = 0;
        $scope.add = function() {
            if($scope.isMax){
                return;
            }
            add({
                id: 'exec-' + num,
                label: 'Exec' + num,
                content: 'Exec' + num,
                start: true,
                execUrl: CONF.SVR_URL.ws + '/exec/' + containerId + '/bash'
            });
            num++;
            if(num >= 5){
                $scope.isMax = true;
            }
        }

        function add(tab) {
            $scope.tabs.push(tab);
            $element.find('.tabular.menu > .item').tab();
        }


        $scope.attachUrl = CONF.SVR_URL.ws + '/attach/' + containerId;
        $scope.attachStart = true;

        $scope.logUrl = CONF.SVR_URL.ws + '/log/' + containerId;
        $scope.logStart = true;

        //semantic初始化
        $element.find('.tabular.menu > .item').tab();

    });

})();

(function() {
    'use strict';

    ngApp.controller('containerCreateCtrl', function($scope, $state, $stateParams, $element, $websocket, dockerApiSvr, containerSvr) {
        'ngInject';

        var imageId = $scope.imageId = $stateParams.imageId;

        $scope.containerCreateVo = {
            imageId: imageId,
            portBindings: [],
            envs: [],
            binds: []
        };

        $scope.portBindingsValidate = function() {
            for (var i in $scope.containerCreateVo.portBindings) {
                var portBinding = $scope.containerCreateVo.portBindings[i];
                if (!portBindingValidate(portBinding)) {
                    return false;
                }
            }
            return true;
        };
        $scope.addPortBinding = function() {
            if (!$scope.portBindingsValidate()) {
                return;
            }

            addPortBinding({
                protocol: 'tcp',
                port: undefined,
                hostPort: undefined                      
            });

        };
        $scope.removePortBinding = function(idx) {
            $scope.containerCreateVo.portBindings.splice(idx, 1);
        };


        $scope.envsValidate = function() {
            for (var i in $scope.containerCreateVo.envs) {
                var env = $scope.containerCreateVo.envs[i];
                if (!envValidate(env)) {
                    return false;
                }
            }
            return true;
        };
        $scope.addEnv = function() {
            if (!$scope.envsValidate()) {
                return;
            }

            addEnv({
                name: undefined,
                value: undefined
            });
        };
        $scope.removeEnv = function(idx) {
            $scope.containerCreateVo.envs.splice(idx, 1);
        };

        $scope.bindsValidate = function() {
            for (var i in $scope.containerCreateVo.binds) {
                var bind = $scope.containerCreateVo.binds[i];
                if (!bindValidate(bind)) {
                    return false;
                }
            }
            return true;
        };
        $scope.addBind = function() {
            if (!$scope.bindsValidate()) {
                return;
            }

            addBind({
                pathHost: undefined,
                path: undefined,
                readOnly: false             
            });
        };
        $scope.removeBind = function(idx) {
            $scope.containerCreateVo.binds.splice(idx, 1);
        };

        $scope.create = function() {
            containerSvr.create($scope.containerCreateVo, function(rsp) {
                if(rsp.status.success){
                    $state.go('container');
                }
            });
        };


        reload();

        function reload() {
            dockerApiSvr.images_inspect($scope.imageId, function(rsp) {
                $scope.image = rsp.data;

                //初始化端口
                for(var i in $scope.image.Config.ExposedPorts){
                    var exposedPort =  i.split('/');
                    addPortBinding({
                        protocol: exposedPort[1],
                        port: parseInt(exposedPort[0]),
                        hostPort: undefined                        
                    });
                }
                //初始化卷
                for(var i in $scope.image.Config.Volumes){
                    var volume = i;

                    addBind({
                        pathHost: undefined,
                        path: i,
                        readOnly: false             
                    });
                }
                //初始化环境变量
                for(var i in $scope.image.Config.Env){
                    var env = $scope.image.Config.Env[i].split('=');

                    addEnv({
                        name: env[0],
                        value: env[1]
                    });
                }                              

                console.log($scope.image);
            });
        };    


        function addPortBinding(data){
            $scope.containerCreateVo.portBindings.push(data);
        }
        function addBind(data){
            $scope.containerCreateVo.binds.push(data);            
        }
        function addEnv(data){
            $scope.containerCreateVo.envs.push(data);            
        }

    });

    function portBindingValidate(portBinding) {
        var intReg = /^\d+$/;
        if (intReg.test(portBinding.port) &&
            intReg.test(portBinding.hostPort)) {
            return true;
        }
        return false;
    }

    function envValidate(env) {
        if (typeof env.name != 'undefined' 
            && typeof env.value != 'undefined') {
            return true;
        }
        return false;
    }
    function bindValidate(bind) {
        if (typeof bind.hostPath != 'undefined' 
            && typeof bind.path != 'undefined'
            && typeof bind.readOnly == 'boolean') {
            return true;
        }
        return false;
    }



})();

(function() {
    'use strict';

    ngApp.controller('containerDetailCtrl', function($scope, $stateParams, $state, $filter, $element, $websocket, ngDialog, dockerApiSvr, containerSvr, CONF) {
        'ngInject';

        var containerId = $scope.containerId = $stateParams.id;
        var statsUrl = CONF.SVR_URL.ws + '/stats/' + containerId;

        var ws = $websocket(statsUrl);

        var statsQueue = undefined;

        ws.onMessage(function(message) {
            statsQueue = containerSvr.countStats($.parseJSON(message.data), statsQueue);
            $scope.stats = statsQueue.stats;
        });

        //切换容器状态
        $scope.switch = function($event) {
                if ($scope.container.State.Status == 'running') {
                    stop();
                } else {
                    start();
                }

                $scope.container.State.Status = 'changing';

                return false;
            }
            //打开命令行
        $scope.openConsole = function() {
            ngDialog.open({
                template: 'app/tpls/dialogs/console.tpl.html',
                scope: $scope,
                width: '90%',
                closeByDocument: false,
                closeByEscape: false,
                className: 'ngdialog-theme-plain'
            });
        }

        //启动容器
        function start() {
            dockerApiSvr.containers_start($scope.container.Id, function(rsp) {
                reload();
            });
        }
        //停止容器
        function stop() {
            dockerApiSvr.containers_stop($scope.container.Id, function(rsp) {
                reload();
            });
        }

        //semantic初始化
        $element.find('.tabular.menu > .item').tab();



        reload();


        function reload() {
            dockerApiSvr.containers_inspect($scope.containerId, function(rsp) {
                $scope.container = rsp.data;
            });
        };


        $scope.$on('$destroy', function() {
            statsQueue = undefined;
            ws.close();
        });


    });

})();

(function() {
    'use strict';

    ngApp.controller('containerListCtrl', function($scope, $element, dockerApiSvr, containerSvr) {
        'ngInject';

        reload();



        //切换容器状态
        $scope.switch = function($event, container) {
            if (container.State == 'running') {
                stop(container);
            } else {
                start(container);
            }
            container.State = 'changing';
            return false;
        }
        //删除容器
        $scope.remove = function(container){
            if(!confirm('确定移除该容器吗？')){
                return;
            }
            containerSvr.remove(container.Id, function(rsp){
                reload();
            });
        }


        //启动容器
        function start(container) {
            dockerApiSvr.containers_start(container.Id, function(rsp) {
                reload();
            });
        }
        //停止容器
        function stop(container) {
            dockerApiSvr.containers_stop(container.Id, function(rsp) {
                reload();
            });
        }



        function reload() {
            dockerApiSvr.containers(function(rsp) {
                $scope.containers = rsp.data;
            });
        }

    });

})();

